:root {
    --primary: #3b82f6;
    --primary-hover: #2563eb;
    --background: #ffffff;
    --foreground: #1f2937;
    --accent: #f3f4f6;
    --accent-hover: #e5e7eb;
  }
  
  .dark {
    --primary: #60a5fa;
    --primary-hover: #3b82f6;
    --background: #1f2937;
    --foreground: #f9fafb;
    --accent: #374151;
    --accent-hover: #4b5563;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
    background-color: var(--background);
    color: var(--foreground);
    margin: 0;
    padding: 0;
  }
  
  .login-container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    background-color: var(--accent);
  }
  
  .login-form-container {
    background-color: var(--background);
    padding: 2rem;
    border-radius: 0.5rem;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    width: 100%;
    max-width: 400px;
  }
  
  .login-title {
    font-size: 1.875rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 1rem;
    color: var(--foreground);
  }
  
  .login-subtitle {
    font-size: 0.875rem;
    text-align: center;
    color: var(--foreground);
    opacity: 0.8;
    margin-bottom: 2rem;
  }
  
  .login-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .form-group {
    position: relative;
  }
  
  .form-input {
    width: 100%;
    padding: 0.75rem 1rem;
    border: 1px solid var(--accent);
    border-radius: 0.25rem;
    font-size: 1rem;
    color: var(--foreground);
    background-color: var(--background);
    transition: border-color 0.3s ease;
  }
  
  .form-input:focus {
    outline: none;
    border-color: var(--primary);
    box-shadow: 0 0 0 2px var(--primary-hover);
  }
  
  .password-toggle {
    position: absolute;
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    color: var(--primary);
    font-size: 0.875rem;
    cursor: pointer;
  }
  
  .form-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.875rem;
  }
  
  .remember-me {
    display: flex;
    align-items: center;
  }
  
  .remember-me-checkbox {
    margin-right: 0.5rem;
  }
  
  .forgot-password {
    color: var(--primary);
    text-decoration: none;
  }
  
  .forgot-password:hover {
    text-decoration: underline;
  }
  
  .login-button {
    width: 100%;
    padding: 0.75rem 1rem;
    background-color: var(--primary);
    color: white;
    border: none;
    border-radius: 0.25rem;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .login-button:hover {
    background-color: var(--primary-hover);
  }
  
  .login-button:disabled {
    opacity: 0.7;
    cursor: not-allowed;
  }
  
  .error-message {
    margin-top: 1rem;
    padding: 0.75rem 1rem;
    background-color: #fee2e2;
    border: 1px solid #f87171;
    border-radius: 0.25rem;
    color: #b91c1c;
    font-size: 0.875rem;
  }
  
  .register-link {
    margin-top: 1.5rem;
    text-align: center;
    font-size: 0.875rem;
    color: var(--foreground);
  }
  
  .register-link a {
    color: var(--primary);
    text-decoration: none;
  }
  
  .register-link a:hover {
    text-decoration: underline;
  }
  
  .sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
  }
  
  @media (max-width: 640px) {
    .login-form-container {
      padding: 1.5rem;
    }
  
    .login-title {
      font-size: 1.5rem;
    }
  
    .form-options {
      flex-direction: column;
      align-items: flex-start;
      gap: 0.5rem;
    }
  }